.siteSearch {
  @apply h-screen overflow-scroll m-auto w-10/12 sm:w-8/12 md:w-7/12 lg:w-6/12 xl:w-5/12 2xl:w-4/12;
  -ms-overflow-style: none;  /* Internet Explorer 10+ */
  scrollbar-width: none;  /* Firefox */
  &::-webkit-scrollbar { 
    display: none;  /* Safari and Chrome */
  }

  .searchInputWrap {
    @apply top-10 relative flex flex-col gap-2 justify-center;
  
    .searchInput {
      @apply px-8 sm:px-10 py-4 sm:py-6 shadow-lg rounded-full bg-white focus:bg-white border-4 outline-none;
      @apply text-lg sm:text-2xl text-gray-600 tracking-wide sm:tracking-widest;

      &:focus {
        @apply shadow-xl;
      }
    }
  }
  
  .searchResultsWrap {
    @apply mt-20 sm:mt-24 mx-auto w-full sm:w-11/12;
  
    .searchResultItemWrap {
      @apply rounded-2xl outline-none;
      @apply mx-1 my-2 sm:my-4 px-8 py-2;
      @apply text-sm sm:text-lg hover:cursor-pointer;
      @apply text-gray-700;

      &:focus {
        @apply border-2 shadow-lg bg-gray-50;
        @apply sm:text-xl;
      }
      &:hover {
        @apply bg-gray-50 drop-shadow;
      }
    }
  }
}
